import React,{useState} from 'react';

const ToDoList = () => {
    const [InputData, setInputData] = useState("");
    const [Items, setItems] = useState([]);

    const addItem=()=>{
        if(!InputData){
            
        }else{
            setItems([...Items,InputData]);
            setInputData("");
        }
       
    }

    const delItem=(ind)=>{
      const Update = Items.filter((elem,id)=>{
        return  id !== ind;
      })
      setItems(Update)
    }

    return (
       <>
           <h1 style={{color:"white",textShadow:'4px 3px 4px black'}} className="headi"> Simple ToDoList</h1> 
           <input className="inp" value={InputData}  type="text" onChange={(e)=> setInputData(e.target.value)}/>
           <button onClick={addItem} className="btn1">Add Item</button>
          

          
           {
            Items.map((element,ind)=>{
                return(
               <div key={ind} className="itm">
                
                <h1 style={{color:"white",textShadow:'4px 3px 4px black'}}>{element}</h1><button className="rbtn" onClick={()=>delItem(ind)}>ðŸ‘ˆ Remove</button>
                
                </div>
               )
            })
           }
           
           
          

      </>
    )
}

export default ToDoList
